@import "../mixin";
@import "../variables";
.gray-background:before {
    content: "";
    position: absolute;
    background-color: #e8e8e8;
    top: 0;
    height: 200px;
    left: 0;
    width: 100%;
    z-index: -1;
} 
.stars {
    margin-top: 7px;
    svg {
        max-width: 13px;
        fill: $star_fill_color;
        stroke: $star_strok_color;
        transform: transition('all',0.3 , 'ease');
    }
}
/*chair*/
#chair-section{
    padding-bottom: 40px;
    .chairParent{
        .chair{
            .title {
            font-size: 1.5rem;
            margin: 0;
            }
            img {
                max-width: 250px;
                height: auto;
                display: block;
                margin: 0 auto;
            }
            .price,.stars {
                display: block;
                margin-top: 7px;
                color: $primary-gray;
            }
            .stars {
                margin-top: 7px;
            }
        }
    } 
    .arrows{
        i.icon{
             font-size: 1.4rem;
             color: $gray-icon-color;
         }
         .arrows_left,
         .arrows_right {
             top: 45%;
             @include translateY(-45%);  
             position: absolute;
             padding: 7px 14px;
             box-shadow: 0 2px 6px 0 rgba(0 0 0 / 5%);
             background-color: $white; 
             border-radius: 2px;
             cursor: pointer; 
         }
         .arrows_left {
             left: 10px;
         }
         .arrows_right { 
             right: 10px;
         }
     }
}
/*rooms*/
#rooms{
    .rooms_category {
        width: 33.33%;
        img {
            width: 100%;
            height: auto;
        }
        .rooms_category-name {
            letter-spacing: 3px;
            color: $white;
            position: absolute;
            top: 50%;
            @include translate(-50%, -50%);
            left: 50%;
            z-index: 1;
            h3 {
                margin-bottom: 2rem;
            }
            .shop-btn {
                padding: 10px 15px;
                background: none;
                color: $white;
                border: solid 2px #fff;
                min-width: 180px;
                display:block;
            }
        }
        &:after {
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: rgba(0, 0, 0, 0.384);
        }
    }
}
/*features*/
#features{
    .features_products {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        .features_product-item {
            display: flex;
            flex-direction: row;
            width: 33.33%;
            margin-top: 25px;
            .product-thumbnail,
            .product-description {
                width: 50%;
            }
            .product-description .product-holder {
                padding: 0 10px;
                .product-price{
                    margin: 10px 0;
                    .product-off {
                        text-decoration: line-through;
                    }
                }
            }
            .product-thumbnail img {
                width: 100%;
                height:auto;
            }
        }
    }
}
/*latest*/
#latest{
    .latest_gallery{
        .latest_gallery-item {
            width: 25%;
            a{
                display: block;
                img {
                    width: 100%;
                    height: auto;
                    display: block;
                }
            }
        }
    }
}
/*about us */
#aboutUs{
    .about-delphinus-holder{
        padding: 0 15px;
    }
    .about-delphinus {
        width: 66.66%;
        .about-delphinus-background{
            background: url("../../assets/images/delphinus/background-1.jpg");
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            line-height: 29px;
            color: $white;
            .about-delphinus-spacing {
                padding:30px;
                h3 {
                    letter-spacing:5px;
                }
                .about-delphinus-description{
                    margin-bottom: 30px;
                }
            }
        }
    }
    .news-letter {
        width: 33.33%;
        .news-letter-spacing {
            padding: 30px 30px 95px;
            border: solid 2px #ddd;
            p.news-letter-desc {
                margin-top: 15px;
            }
        }
        form {
            input {
                padding: 10px 20px;
                border: solid 2px #ddd;
                margin: 30px 0;
                width:100%;
            }
            button[type="submit"] {
                position: absolute;
                right:13px;
                top: 50%;
                @include translateY(-50%);
                color: $gray-icon-color;
                background:none;
                border: none;
            }
        }
    }
    .aboutUs_rows-images{
        .aboutUs_rows-images-item {
            width: 33.33%;
            .aboutUs_rows-images-item-holder{
                padding:30px 15px;
                img {
                    width: 100%;
                    height: auto;
                    @include transition(all,0.3s,ease);
                    &:hover{
                    @include scale(1.1,1.1);
                    }
                } 
            }
        }
    }
    .aboutUs_rows-brands{
        padding:50px 15px;
        .aboutUs_rows-brands-holder{
            padding: 0 80px;
            .aboutUs_rows-brands-item {
                width: 16.66%;
                opacity: 0.6;
                text-align: center;
                @include transition( opacity,0.3s ,ease);
                &:hover{
                    opacity: 1;
                    cursor: pointer;
                }
            }
        }
    }
}
#free-shipping {
    background-color: $light-color;
    padding: 80px 0;
    .free-shipping-box-item {
        width: 50%;
        &:first-child h3{
            margin-right: 20px;
        }
        &:last-child h3{
            margin-left: 20px;
        }
        h3 {
            background-color: $white;
            padding: 40px 0;
            font-weight: bold;
            color: $black;
            font-size:1.7rem;
        }
    }
}

