@mixin transition($element:all , $time , $form:ease-in) {
    transition: $element $time $form;
}
@mixin scale($x,$y) {
    transform: scale($x,$y);
}
@mixin translate($x ,$y){
    transform: translate($x, $y);
}
@mixin translateX($x){ 
    transform: translateX($x);
}
@mixin translateY($y){  
    transform: translateY($y);
}
